

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import { mapState } from 'vuex'
import $ from 'jquery'
import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import event from '../../../js/Event.js'
import { getYMDFormateTime, isArrayType } from "../../../js/utility.js"

export default {
name: 'project',
components: {
Flexbox,
FlexboxItem,
XTextarea,
Popup
},
computed: {
...mapState([
'projectList'
]),
popupTitle() {
if (this.popupType === 1) {
return '添加';
} else if (this.popupType === 2) {
return '编辑';
}
}
},
data() {
return {
p: 1,
nav_list: [],
nav_type: 1,
itemlist: [],
addPopupShow: false,
popupContent: '',
popupType: 1, //1 是添加  2 是编辑
popupItem: null,
}
},
mounted() {
event.$on('gotoAddData', function() {
this.popupItem = null;
this.popupContent = '';
this.popupType = 1;
this.addPopupShow = true;
}.bind(this));
if (this.projectList.permission_list) {
this.$store.commit('updateXheaderShowAdd', true);
}

this.nav_list = [{ name: '我关注的', type: 1 }, { name: '我负责的', type: 2 }];

this.nav_type = this.projectList.nav_type ? this.projectList.nav_type : 1;
this.handleCache(this.nav_type);
},
beforeRouteLeave(to, from, next) {
if (to.name === 'tasklist' || to.name === 'tasksearch') {
this.saveCache(this.nav_type);
this.projectList.nav_type = this.nav_type;
this.$store.commit('updateProjectList', this.projectList);
} else {
this.$store.commit('updateProjectList', {});
}

next();
},
beforeDestroy() {
event.$off("gotoAddData");
},
methods: {
infinite(done) {
this.getList(done);
},
refresh(done) {
this.p = 1;
this.getList(done);
},
getList(done) {
var postParams = {};
postParams['p'] = this.p;
if (this.nav_type === 1) {
postParams['by'] = 'about'; 
} else if (this.nav_type === 2) {
postParams['by'] = 'own'; 
}

AjaxSilentHttp('POST', 'm=task&a=typelist', postParams, this, function(res, wself, success) {
if (success && isArrayType(res.list)) {

if (res.permission_list.add === 1) {
wself.projectList.permission_list = true;
wself.$store.commit('updateXheaderShowAdd', true);
} else {
wself.projectList.permission_list = false;
wself.$store.commit('updateXheaderShowAdd', false);
}

for (var i = 0; i < res.list.length; i++) {
var item = res.list[i];
item.create_time = '创建于 ' + getYMDFormateTime(item.create_time);
item.count = item.done_count + '/' + item.sum_count;
item.percent = parseInt(parseInt(item.done_count) * 100 / parseInt(item.sum_count));
}

if (wself.p === 1) {
wself.itemlist = res.list;
} else {
wself.itemlist = wself.itemlist.concat(res.list);
}

if (typeof done === 'function') {
if (wself.p === 1 && res.list.length < 10) {
done(true);
} else if (res.list.length === 0) {
done(true);
} else {
done();
}
}
wself.p = wself.p + 1;
}
});
},
clickNav(type) {
if (this.nav_type !== type) {
this.saveCache(this.nav_type); 
this.nav_type = type;
this.itemlist = [];
this.p = 1;
this.handleCache(this.nav_type); 
}
},
handleCache(nav_type) { //操作缓存
if (nav_type === 1 && this.projectList.about) {
if (this.projectList.about.refresh) {
this.p = 1;
this.itemlist = [];
} else {
this.itemlist = this.projectList.about.itemlist;
this.p = this.projectList.about.p;
this.$nextTick(() => {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, this.projectList.about.scroll, false);
if (this.projectList.about.loadingState === 2) {
this.$refs.my_scroller.finishInfinite(true);
}
});
}

} else if (nav_type === 2 && this.projectList.own) {
if (this.projectList.own.refresh) {
this.p = 1;
this.itemlist = [];
} else {
this.itemlist = this.projectList.own.itemlist;
this.p = this.projectList.own.p;
this.$nextTick(() => {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, this.projectList.own.scroll, false);
if (this.projectList.own.loadingState === 2) {
this.$refs.my_scroller.finishInfinite(true);
}
});
}
} else {
this.$refs.my_scroller.finishInfinite(false);
}
},
saveCache(nav_type) {
var type = '';
if (nav_type === 1) {
type = 'about'; 
} else if (nav_type === 2) {
type = 'own'; 
}
this.projectList[type] = {
type: type, //平台类型与type相同 进行赋值
itemlist: this.itemlist,
p: this.p,
nav_type: this.nav_type,
refresh: false,
scroll: this.$refs.my_scroller.getPosition().top,
loadingState: this.$refs.my_scroller.loadingState,
};
},
enterTaskList(item) {
if (this.$route.params.action === 'default') {
this.$router.push({
name: 'tasklist',
params: {
id: item.id,
action: 'default'
}
});
} else if (this.$route.params.action === 'select') {
event.$emit('selectedRelateItem', { type: 'project', item: item });
this.$router.go(-1);
}
},
popupSave() {
if (this.popupContent === '') {
this.$vux.toast.text('请填写列表名', 'top');
}
var url = '';
var postParams = {};
postParams.name = this.popupContent;
if (this.popupType === 1) {
url = 'm=task&a=addtype';
} else if (this.popupType === 2) {
url = 'm=task&a=edittypeajax';
postParams.type_id = this.popupItem.id;
}

AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.$refs.my_scroller.triggerPullToRefresh();
wself.addPopupShow = false;
}
});
},
editProjectName(item) {
this.popupItem = item;
this.popupContent = item.name;
this.popupType = 2;
this.addPopupShow = true;
}
}
}




